import { useEffect, useRef, useState } from 'react';
import { useDispatch } from 'react-redux';
import { deleteFromPendings, toggleCompleteTask } from '../../../store/slices';

export function useCard(id: string) {
	const [expanded, setExpanded] = useState(false);

	const dispatch = useDispatch();

	const deletePending = () => dispatch(deleteFromPendings({ id }));
	const donePending = () => dispatch(toggleCompleteTask({ id }));

	const toggleExpanded = () => {
		setExpanded(!expanded);
	};

	const typographyRef = useRef<HTMLDivElement>(null);
	const [isWrapped, setIsWrapped] = useState(false);
	useEffect(() => {
		const typographyElement = typographyRef.current;
		if (typographyElement) {
			setIsWrapped(
				typographyElement.scrollWidth > typographyElement.clientWidth
			);
		}
	}, []);

	const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
		donePending();
	};
	return {
		deletePending,
		toggleExpanded,
		isWrapped,
		expanded,
		typographyRef,
		handleChange,
	};
}
