import { createSlice } from '@reduxjs/toolkit';
import { CardProps } from '../../components/organisms/card/Card.props';

export interface PendingListProps extends CardProps {}
interface PendingsInterface {
	tasksList: PendingListProps[];
	totalDone: number;
	totalActive: number;
}
const initialState: PendingsInterface = {
	tasksList: [
		{
			id: '0',
			title: 'Make the Bed',
			status: 'completed',
			priority: 'high',
			time: '21 March 2023',
		},
		{
			id: '1',
			title: 'Buy Dog Food',

			description: 'only 4 Kg because it last long',
			status: 'active',
			priority: 'low',
			time: '23 July 2023',
		},
		{
			id: '2',
			title: 'Go to the Vet',
			status: 'active',
			priority: 'high',
			time: '21 July 2023',
		},
		{
			id: '3',
			title: 'Finish HomeWork',
			description: 'Ir al veterinario',
			status: 'active',
			priority: 'low',
			time: '22 March 2023',
		},
	],
	totalDone: 0,
	totalActive: 0,
};
export const pendingsSlice = createSlice({
	name: 'pendings',
	initialState,
	reducers: {
		addToPendings(state, action) {
			const newPendings = action.payload;

			state.tasksList.push({
				title: newPendings.title,
				id: newPendings.id,
				priority: newPendings.priority,
				description: newPendings.description,
				status: newPendings.status,
				time: newPendings.time,
			});
		},
		deleteFromPendings(state: PendingsInterface, action) {
			const id = action.payload.id;
			const existingPending = state.tasksList.find(
				pending => pending.id === id
			);
			if (existingPending) {
				state.tasksList = state.tasksList.filter(pending => pending.id !== id);
			}
		},
		toggleCompleteTask(state, action) {
			const id = action.payload.id;
			const existingTask = state.tasksList.find(task => task.id === id);
			if (existingTask) {
				const task = state.tasksList.find(task => task.id === id);
				if (task) {
					task.status = task.status == 'completed' ? 'active' : 'completed';
					state.totalDone++;
				}
			}
		},
	},
});

export const { addToPendings, deleteFromPendings, toggleCompleteTask } =
	pendingsSlice.actions;
