import { SortableItem } from '../';
import { Grid } from '@mui/material';
import { PendingListProps } from '../../../store/slices/pendingSlice';
import { DndContext, closestCenter } from '@dnd-kit/core';
import { SortableContext, rectSortingStrategy } from '@dnd-kit/sortable';
import { useSortableList } from './useSortableList';

export const SortableList = ({ tasks, setTasks }: any) => {
	const { sensors, handlerDragEnd } = useSortableList(setTasks);
	return (
		<DndContext
			sensors={sensors}
			collisionDetection={closestCenter}
			onDragEnd={handlerDragEnd}>
			<Grid sx={{ my: 3 }}>
				<SortableContext items={tasks} strategy={rectSortingStrategy}>
					{tasks?.map((pending: PendingListProps) => (
						<SortableItem
							title={pending.title}
							key={pending.id}
							id={pending.id}
							description={pending.description}
							priority={pending.priority}
							status={pending.status}
							time={pending.time}
						/>
					))}
				</SortableContext>
			</Grid>
		</DndContext>
	);
};
